import { Component, OnInit } from '@angular/core';
import { researchanddevelopment } from '../services/researchanddevelopment.service';
import { data } from '../models/data.model';

@Component({
  selector: 'app-research-development',
  templateUrl: './research-development.component.html',
  styleUrls: ['./research-development.component.css']
})
export class ResearchDevelopmentComponent implements OnInit {
  Bind: data = {} as data;

  constructor(private ResearchAndDevelopmentService : researchanddevelopment) { 
  }

  ngOnInit() {
    this.dataBind();
  }

  async dataBind(){
    this.Bind.field1 = await this.ResearchAndDevelopmentService.getDataField1();
    this.Bind.field2 = await this.ResearchAndDevelopmentService.getDataField2();
    this.Bind.field3 = await this.ResearchAndDevelopmentService.getDataField3();
    this.Bind.field4 = await this.ResearchAndDevelopmentService.getDataField4();
    this.Bind.field5 = await this.ResearchAndDevelopmentService.getDataField5();
    this.Bind.field6 = await this.ResearchAndDevelopmentService.getDataField6();
    this.Bind.field7 = await this.ResearchAndDevelopmentService.getDataField7();
    this.Bind.field8 = await this.ResearchAndDevelopmentService.getDataField8();
  }

}
